import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DetailComponent } from './detail.component';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { BillDetailModule } from 'src/app/cafe/components/bill-detail/bill-detail.module';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';

const routes: Routes = [
  {
    path: '',
    children: [
      {
        path: "",
        component: DetailComponent
      },
    ]
  },
]


@NgModule({
  declarations: [DetailComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule,
    BillDetailModule
  ],
  providers :[VhComponent]
})
export class DetailModule { }
