import { DeliveryGoodsComponent } from 'src/app/cafe/components/bill-detail/delivery-goods/delivery-goods.component';
import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhQueryCafe, VhAlgorithm } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { PurchaseComponent } from 'src/app/cafe/components/bill-detail/purchase/purchase.component';
import { ReturnSupplierComponent } from 'src/app/cafe/components/bill-detail/return-supplier/return-supplier.component';
import { ExportKitchenComponent } from 'src/app/cafe/components/bill-detail/export-kitchen/export-kitchen.component';
import { ImportKitchenComponent } from 'src/app/cafe/components/bill-detail/import-kitchen/import-kitchen.component';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss'],
})
export class DetailComponent {
  detail: any;
  inOutReturnProduct: any = this.init();
  reportConfig: any = JSON.parse(localStorage.getItem('vhsales-report'));
  stateObj: any;
  init() {
    return { sum_products: 0, total_atax: 0, total_btax: 0, list: [] };
  }

  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    private cdRef: ChangeDetectorRef,
    public vhAlgorithm: VhAlgorithm,
    private languageService: LanguageService,
    private datePipe: DatePipe,
    private vhComponent: VhComponent
  ) {
    let data: any = this.router.getCurrentNavigation().extras.state;
    if (data) {
      this.stateObj = data.stateObj;
      this.detail = data.detail;
      if (data.inOutReturnProduct)
        this.inOutReturnProduct = data.inOutReturnProduct;
      else this.getInOutReturnDetail();
    }
  }

  heightScroll: string;
  widthScroll: string;
  ngAfterViewChecked() {
    if (
      document.querySelector('.height-layout') &&
      document.querySelector('.height-header') &&
      document.querySelector('.ant-table-thead')
    ) {
      this.heightScroll =
        document.querySelector('.height-layout').clientHeight -
        document.querySelector('.height-header').clientHeight -
        document.querySelector('.ant-table-thead').clientHeight -
        90 +
        'px';
    }
    if (document.querySelector('.height-layout')) {
      this.widthScroll =
        document.querySelector('.height-layout').clientWidth + 'px';
    }
    this.cdRef.detectChanges();
  }

  /**
   * get dữ liệu báo cáo
   * @example this.getInOutReturnDetail()
   */
  getInOutReturnDetail() {
    let IDsub_or_product = this.detail['id_subproduct']? this.detail['id_subproduct']: this.detail['id_product']
    this.vhQueryCafe
      .getReportInputOutputReturns_Detail_byTime_byIDMaterial_byBillTypeArray(
        this.stateObj.inOutReturnProduct.startTime,
        this.stateObj.inOutReturnProduct.endTime,
        IDsub_or_product,
        this.stateObj.goodsFilter,
        this.stateObj['id_branch_report']
      )
      .then((res) => {
        console.log(
          'getReportInputOutputReturns_Detail_byTime_byIDMaterial_byBillTypeArray',
          res
        );

        this.inOutReturnProduct['list'] = res.docs;
        this.inOutReturnProduct['sum_products'] = res.quantity;
        this.inOutReturnProduct['total_btax'] = res.pay_btax;
        this.inOutReturnProduct['total_atax'] = res.pay_atax;
      });
  }

  /**
   * mở modal component xem thông tin bill của từng loại bill-type tương ứng
   * @param item 
   * @example this.goToDetail(bill_selected)
   */
  goToDetail(bill) {
    if (bill.bill_type == 30 || bill.bill_type == 31) {
      this.vhComponent
        .showModal(
          DeliveryGoodsComponent,
          { id_bill: bill.id_bill },
          false,
          false,
          'bill-detail'
        )
        .then((modal) => {
          modal.onWillDismiss().then((dataReturn) => console.log(dataReturn));
        });
    }
    if(bill.bill_type == 2){
      this.vhComponent
      .showModal(
        PurchaseComponent,
        { id_bill: bill.id_bill },
        false,
        false,
        'bill-detail'
      )
      .then((modal) => {
        modal.onWillDismiss().then((dataReturn) => console.log(dataReturn));
      });
    }
    if(bill.bill_type == 10){
      this.vhComponent
      .showModal(
        ReturnSupplierComponent,
        { id_bill: bill.id_bill },
        false,
        false,
        'bill-detail'
      )
      .then((modal) => {
        modal.onWillDismiss().then((dataReturn) => console.log(dataReturn));
      });
    }
    
    if(bill.bill_type == 3) {
      return this.vhComponent.showModal(ExportKitchenComponent, { id_bill: bill.id_bill }, false, false, 'bill-detail')
        .then(modal => {
          modal.onWillDismiss().then(dataReturn => console.log(dataReturn))
        })
    }
    if(bill.bill_type == 16) {
      return this.vhComponent.showModal(ImportKitchenComponent, { id_bill: bill.id_bill }, false, false, 'bill-detail')
        .then(modal => {
          modal.onWillDismiss().then(dataReturn => console.log(dataReturn))
        })
    }
  }

  format_date(date) {
    return this.datePipe.transform(date, 'dd_MM_yyyy');
  }

  /**
   * trả về nội dung của bill
   * @param bill_type 
   * @param id_bill 
   * @returns string
   * @example let method = this.setType(1)
   */
  setType(value) {
    switch (value) {
      case 1:
        return this.languageService.translate('Sold');
      case 2:
        return this.languageService.translate('Purchase');
      case 3:
        return this.languageService.translate('For kitchen');
      case 10:
        return this.languageService.translate('Returned');
      case 16:
        return this.languageService.translate('Returned from kitchen');
      case 30:
        return this.languageService.translate(
          'Ingredients issued to other branches'
        );
      case 31:
        return this.languageService.translate(
          'Ingredients entered from other branches'
        );
    }
  }
  /**
   * trả vè tên loại báo cáo để gán vô tên file excel tải về
   * @param type 
   * @returns string
   * @example let report_name = this.getNameReport(1)
   */
  getNameReport(type) {
    switch (type) {
      case 1:
        return this.languageService.translate(
          '_ingredients_return_to_supplier_report'
        );
      case 2:
        return this.languageService.translate(
          '_ingredients_purchased_from_supplier_report'
        );
      case 3:
        return this.languageService.translate(
          '_ingredients_for_kitchen_report'
        );
      case 4:
        return this.languageService.translate(
          '_ingredients_returned_from_kitchen_report'
        );
      case 5:
        return this.languageService.translate('_ingredients_for_sales_report');
      case 6:
        return this.languageService.translate(
          '_total_number_of_ingredients_issued_report'
        );
      case 7:
        return this.languageService.translate(
          '_total_number_of_ingredients_purchased_report'
        );
      case 8:
        return this.languageService.translate(
          '_total_number_of_ingredients_issued_report'
        );
      case 9:
        return this.languageService.translate(
          '_total_number_of_ingredients_purchased_report'
        );
    }
  }

  status: boolean = false;
  /**
  * xuất dữ liệu ra file excel và tải về
  */
  export() {
    this.status = true;
    let data = new Array();
    let date = `${this.format_date(
      this.stateObj.startTime
    )}_${this.languageService.translate('To')}_${this.format_date(
      this.stateObj.endTime
    )}`;
    let name = `${this.languageService.translate(
      'Input-output-return'
    )}_${date}_${
      this.stateObj['id_branch_report'] == 'all'
        ? ''
        : this.vhAlgorithm
            .changeAlias(
              this.vhQueryCafe.getlocalBranch(this.stateObj['id_branch_report'])
                .name
            )
            .replace(/\s/g, '_')
    }`;
    let total: any = new Object();
    total[this.languageService.translate('Invoice code')] = '';
    total[this.languageService.translate('Date')] = '';
    total[this.languageService.translate('Product name')] =
      this.languageService.translate('Total');
    total[this.languageService.translate('Unit')] = '';
    total[this.languageService.translate('Type')] = '';
    total[this.languageService.translate('Quantity')] =
      this.inOutReturnProduct['sum_products'];

    data.push(total);

    for (let _rows of this.inOutReturnProduct['list']) {
      let item: any = new Object();
      item[this.languageService.translate('Invoice code')] = _rows.bill_code;
      item[this.languageService.translate('Date')] = this.format_date(
        _rows.date
      );
      item[this.languageService.translate('Product name')] = _rows.name;
      item[this.languageService.translate('Unit')] = _rows.unit;
      item[this.languageService.translate('Type')] = this.setType(
        _rows.bill_type
      );
      item[this.languageService.translate('Quantity')] = _rows.quantity;

      data.push(item);
    }

    this.vhAlgorithm
      .exportXLSX(data, name)
      .then(() => (this.status = false))
      .catch((err) => (this.status = false));
  }
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/report/input-output-return'], {
      state: { stateObj: this.stateObj },
    });
  }
}
